import platform from '../assets/platform.png'
import hills from '../assets/hills.png'
import background from '../assets/background.png'
import platformSmallTall from '../assets/platformSmallTall.png'


import spriteRunLeft from '../assets/spriteRunLeft.png'
import spriteRunRight from '../assets/spriteRunRight.png'
import spriteStandLeft from '../assets/spriteStandLeft.png'
import spriteStandRight from '../assets/spriteStandRight.png'


const canvas =
document.querySelector('canvas');


const c = canvas.getContext('2d');


canvas.width = 1024
canvas.height = 576

const gravity = 2.5;

class Player{
    constructor() {
        this.speed = 10
        this.position = { x: 100, y: 100 }
        this.velocity = { x: 0, y: 0}
        this.width= 50
        this.height= 110
        this.image = createImage(spriteStandRight)
        this.frames = 0
        this.sprites = {
            stand: {
                right: createImage(spriteStandRight),
                left: createImage(spriteStandLeft),
                cropWidth: 177,
                width: 66
            },
            run: {
                right: createImage(spriteRunRight),
                left: createImage(spriteRunLeft),
                cropWidth: 341,
                width: 114.875 
            }
        }
        this.currentSprite = this.sprites.stand.right
        this.currentCropWhidth = 177
    }

    draw(){
        c.drawImage(
            this.currentSprite,
            this.currentCropWhidth * this.frames,
            0,
            this.currentCropWhidth,
            400, 
            this.position.x, 
            this.position.y,
             this.width,
             this.height
             )
    }

    update(){
        this.frames++
        if(this.frames > 59 && (this.currentSprite === 
            this.sprites.stand.right ||
            this.currentSprite === this.sprites.stand.left)
            ){
            this.frames = 0
        }
       else if(this.frames > 29 && (this.currentSprite === 
            this.sprites.run.right || 
            this.currentSprite === this.sprites.run.left)
            
            ){
            this.frames = 0
        }

        
        this.draw()
        this.position.y += this.velocity.y
        this.position.x += this.velocity.x

        if(this.position.y + this.height + 
            this.velocity.y <= canvas.height)
            {
                this.velocity.y += gravity
            }
    
        };
}

class Platform {
    constructor({x, y, image}){
        this.position = { x, y }
        this.image = image
        this.width = image.width
        this.height = image.height
    }

    draw(){
        c.drawImage(this.image, this.position.x, this.position.y) 
    }
}

/* clasee */
class GenericObject {
    constructor({x, y, image}){
        this.position = { x, y }
        this.image = image
        this.width = image.width
        this.height = image.height
    }
    draw(){
        c.drawImage(this.image, this.position.x, this.position.y) 
    }
}

/* function */
function createImage(imageSrc){
    const image = new Image();
    image.src = imageSrc
    return image    
}

    
let platformImage = createImage(platform)
let backgroundImage = createImage(background)
let hillsImage = createImage(hills)
let smallTall = createImage(platformSmallTall)

  /* class intance and new objects */
let player = new Player();
let platforms = []
let genericObjects = []

let lastKey

let keys = {
    right: { pressed: false },
    left: { pressed: false },
} 

let scrollOffset = 0;


function init(){

 platformImage = createImage(platform)
 backgroundImage = createImage(background)
 hillsImage = createImage(hills)

/* objects instance */
 player = new Player();
 platforms = [
    new Platform({ 
        x:platformImage.width * 4 + 10 -2 + platformImage.width - smallTall.width,
        y:270, 
        image: smallTall }),
        new Platform({ 
            x:platformImage.width * 4 + 300 -2,
            y:470, 
            image: smallTall }), 
            new Platform({ 
                x:platformImage.width * 4 + 900 -2,
                y:470, 
                image: smallTall }),
     new Platform({
        x: -1,
        y:470,
        image: platformImage
    }),
    new Platform({
        x:platformImage.width - 3,
        y:470, 
        image: platformImage
     }),
    new Platform({ 
        x:platformImage.width * 2 + 100,
        y:470, 
        image: platformImage
     }),
     new Platform({ 
        x:platformImage.width * 3 + 300,
        y:470, 
        image: platformImage }), 
        new Platform({ 
        x:platformImage.width * 5 + 400 -2,
        y:370, 
        image: smallTall 
        }),   
         

    new Platform({ 
        x:platformImage.width * 6 + 400, 
        y:470, 
        image: smallTall 
    }),

    new Platform({ 
        x:platformImage.width * 7 + 500 , 
        y:470, 
        image: smallTall 
    }),
    new Platform({ 
        x:platformImage.width * 8 + 600 , 
        y:470, 
        image: smallTall 
    }),

    new Platform({ 
        x:platformImage.width * 8 + 700 , 
        y:470, 
        image: smallTall 
    }),
    new Platform({ 
        x:-10 , 
        y:470, 
        image: smallTall 
    })
]

 genericObjects = [
    new GenericObject({
        x: -150,
        y: -1,
        image: backgroundImage
    }),
    new GenericObject({
    x: -1,
    y: -1,
    image: backgroundImage
}),

 new GenericObject({
    x: -1,
    y: -1,
    image: hillsImage
}),
new GenericObject({
    x: -500,
    y: -1,
    image: hillsImage
}),
new GenericObject({
    x: -1,
    y: -1,
    image: hillsImage
}),
new GenericObject({
    x: hillsImage.width * 2 + 100,
    y: -1,
    image: hillsImage
}),

new GenericObject({
    x: hillsImage.width * 4 + 400 ,
    y: -1,
    image: hillsImage
}),

new GenericObject({
    x: hillsImage.width * 6 + 500 ,
    y: -1,
    image: hillsImage
}),


]

scrollOffset = 0
}

init()
function animate(){
    requestAnimationFrame(animate);
     c.fillStyle ='white'
    c.fillRect(0, 0, canvas.width, canvas.height);

    genericObjects.forEach( (genericObject) =>{
        genericObject.draw()
    })

    platforms.forEach((platform) => {
        platform.draw()    
    })
    player.update();



    
    if(keys.right.pressed && player.position.x < 400)
        { player.velocity.x = player.speed  }
        
        
    else if((keys.left.pressed && player.position.x > 100)
     || (keys.left.pressed && scrollOffset === 0 && player.position.x > 0)
    )
        { player.velocity.x = -player.speed; } 

        else {
             player.velocity.x = 0;

        if(keys.right.pressed){
            scrollOffset += player.speed

            platforms.forEach((platform) => {
                platform.position.x -= player.speed ; 
            })

            genericObjects.forEach((genericObject) =>{
                genericObject.position.x -= player.speed*.66
            })
        }
        else if (keys.left.pressed && scrollOffset > 0){
           scrollOffset -= player.speed

           platforms.forEach((platform) =>{
                platform.position.x += player.speed
          })

          genericObjects.forEach((genericObject) =>{
            genericObject.position.x += player.speed*.66
        })
        }
    }


    /*platform collision detecting  */
    platforms.forEach(platform => {
        if(player.position.y + player.height <= 
            platform.position.y && 
            player.position.y + player.height +
            player.velocity.y >= 
            platform.position.y &&
            player.position.x + player.width >=
            platform.position.x && 
            player.position.x <=
            platform.position.x + platform.width)
            {  player.velocity.y = 0 };     
        }); 
            /* srite switching condicional */
        if(
            keys.right.pressed &&
            lastKey === 'right' && 
            player.currentSprite !== player.sprites.run.right)
            {
            player.frames = 1
            player.currentSprite = player.sprites.run.right
            player.currentCropWhidth = player.sprites.run.cropWidth
            player.width = player.sprites.run.width
        } else if(
            keys.left.pressed &&
            lastKey === 'left' && 
            player.currentSprite !== player.sprites.run.left)
            {
                player.currentSprite = player.sprites.run.left
                player.currentCropWhidth = player.sprites.run.cropWidth
                player.width = player.sprites.run.width
            }
            else if(
                !keys.left.pressed &&
                lastKey === 'left' && 
                player.currentSprite !== player.sprites.stand.left){
                player.currentSprite = player.sprites.stand.left
                player.currentCropWhidth = player.sprites.stand.cropWidth
                player.width = player.sprites.stand.width
                }

                else if(
                    !keys.right.pressed &&
                    lastKey === 'right' && 
                    player.currentSprite !== player.sprites.stand.right){
                    player.currentSprite = player.sprites.stand.right
                    player.currentCropWhidth = player.sprites.stand.cropWidth
                    player.width = player.sprites.stand.width
                    }
        
            /* win condition */
        if(scrollOffset > platformImage.width * 5 + 700 - 2){
           console.log("you win")        
      };

        // lose condition
        if(player.position.y > canvas.height){
           console.log("you lose")        
            init()
        }
}

animate();

addEventListener('keydown', (event)=> {
    var codigo = event.key;
    switch (codigo) {
        case 'a': keys.left.pressed = true
       lastKey = 'left'
            break;
        case 's':
        break;
        case 'd':
        keys.right.pressed = true
        lastKey = 'right'

        break; 
        case 'w': 
        player.velocity.y -= 26;
        break; 
        case ' ': 
        player.velocity.y -= 26;
    }
});

addEventListener('keyup', (event)=> {
    var codigo = event.key;
    switch (codigo) {
        case 'a': keys.left.pressed = false;
            break;
        case 's':
        break;
        case 'd': keys.right.pressed = false;
       
            break; 
        case 'w':
        
        break;     
    }
});